import { defineComponent, computed, ref, nextTick } from 'vue';
import { useStore } from '@/store';
import { menuMapLeafKeys } from '@/utils/map-menus';
import pageSearch from '@/components/page-search';
import pageContent from '@/components/page-content';
import pageModal from '@/components/page-modal';
import { searchFormConfig } from './config/search.config';
import { contentTableConfig } from './config/content.config';
import { modalConfig } from './config/modal.config';
import { usePageModal } from '@/hooks/use-page-modal';
export default defineComponent({
  name: 'role',
  components: {
    pageSearch,
    pageContent,
    pageModal
  },

  setup() {
    //1.pageModalhook
    const elTreeRef = ref();

    const editCallback = item => {
      const leftKeys = menuMapLeafKeys(item.menuList);
      nextTick(() => {
        elTreeRef.value?.setCheckedKeys(leftKeys, false);
      });
    };

    const [pageModalRef, defaultInfo, handleNewData, handleEditData] = usePageModal(undefined, editCallback);
    const store = useStore();
    const menus = computed(() => store.state.entireMenu);
    const otherInfo = ref({});

    const handleCheckChange = (data1, data2) => {
      const checkedKeys = data2.checkedKeys;
      const halfCheckedKeys = data2.halfCheckedKeys;
      const menuList = [...checkedKeys, ...halfCheckedKeys];
      otherInfo.value = {
        menuList
      };
    };

    return {
      searchFormConfig,
      contentTableConfig,
      modalConfig,
      pageModalRef,
      defaultInfo,
      handleNewData,
      handleEditData,
      menus,
      otherInfo,
      handleCheckChange,
      elTreeRef
    };
  }

});